<template>
<div class="dingdan">
<el-breadcrumb separator="/">
  <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
  <el-breadcrumb-item><a href="/"></a></el-breadcrumb-item>
  <el-breadcrumb-item></el-breadcrumb-item>
</el-breadcrumb>


<el-card class="box-card">
   <el-row>
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="input3"
            class="input-with-select"
            @keyup.enter.native="search()"
          >
            <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <el-table :data="shuju" stripe style="width: 100%">
        <el-table-column label="id" type="index"></el-table-column>
        <el-table-column prop="order_number" label="" width="180"></el-table-column>
        <el-table-column prop="order_price" label=""></el-table-column>
        <el-table-column prop="order_pay" label="">
            <template slot-scope="scope">
                <el-tag type="danger" v-if="scope.row.pay_status==0"></el-tag>
                <el-tag type="success" v-else></el-tag>
            </template>
        </el-table-column>
          <el-table-column prop="is_send" label=""></el-table-column>
            
        <el-table-column label="">
          <template slot-scope="scope">{{timestampToTime(scope.row.create_time)}}</template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="bij"></el-button>
            <el-button type="success" icon="el-icon-location
" size="mini" @click="addressinfo(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>

       <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="canshu.pagenum"
        :page-sizes="[1, 3, 5, 7]"
        :page-size="canshu.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>

       <el-dialog title="" :visible.sync="bjdialogVisible" width="50%" @close="guanbi">
        <el-form label-width="100px" :model="bjname" :rules="rules" ref="bjrule">
          <el-form-item label="/" prop="address1">
             <el-cascader
    v-model="bjname.address1"
    :options="citydata"
    :props="{ expandTrigger: 'hover' }"
    @change="handleChange"></el-cascader>
             
          </el-form-item>
          <el-form-item label="" prop="address2">
            <el-input v-model="bjname.address2"></el-input>
             
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="bjdialogVisible=false"> </el-button>
          <el-button type="primary" @click="qd"> </el-button>
        </span>
      </el-dialog>


      
       <el-dialog title="" :visible.sync="zsdialogVisible" width="70%" >
         <el-timeline>
        <el-timeline-item
      v-for="(item, index) in wuliu"
      :key="index"
      :icon="icon"
      :type="type"
      :color="hcolor"
      :size="size"
     
      >
       {{item.time}}
      {{item.context}}
     
    </el-timeline-item>
  </el-timeline>
      </el-dialog>
</el-card>
</div>
</template>
<script>
import {dingdan,wuliu} from 'network/shouye/dingdan/dingdan'
import citydata from "@/sc/citydata.js"
export default {
  name: "component_name",
  data () {
    return {
        canshu:{
            query:"",
            pagenum:1,
            pagesize:7
        },
        shuju:[],
        input3:"",
        total:1,
        bjdialogVisible:false,
         rules: {
        address1: [
          { required: true, message: "/", trigger: "blur" },
          { min: 2, max: 50, message: " 2  50 ", trigger: "blur" }
        ],
        address2: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 50, message: " 2  50 ", trigger: "blur" }
        ]
      },
      bjname:{
address1:[],
address2:""
      },
      selectid:[],
      citydata,
      zsdialogVisible:false,
      wuliu:[],
      size: 'normal',
      type: 'primary',
      icon: 'el-icon-more',
      hcolor:'#0bbd87'
    };
  },
  methods: {
      handleSizeChange(a){
this.canshu.pagesize=a
this.jcfz(this.canshu)
      },
      handleCurrentChange(b){
this.canshu.pagenum=b
this.jcfz(this.canshu)
      },
      //
      jcfz(data){
          dingdan(data).then(res=>{
          this.xt(res,200,"")
           const {data:res1}=res
           this.shuju=res1.data.goods
           this.total=res1.data.total
           console.log(res1)
          })
      },
      search(){
    this.canshu.query=this.input3
      this.jcfz(this.canshu)
      },
      bij(){
        this.bjdialogVisible=true
      },
      qd(){

      },
      gaibian(){

      },
      handleChange(){

      },
      guanbi(){
       this.$refs.bjrule.resetFields()
      },
      addressinfo(e){
          this.zsdialogVisible=true
        wuliu(e.order_id).then(res=>{
           const {data:res1}=res
           if(res1.meta.status!=200) return this.$message.error("")
         this.wuliu=res1.data
         console.log(this.wuliu)
         
        })
      },
      //
      xt(res,codeshu,sb){
        const {data:res1}=res
        if(res1.meta.status!=codeshu) return this.$message.error(`${sb}`)
      },

       timestampToTime(timestamp) {
      var date = new Date(timestamp * 1000); //10*1000131000
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      var h = date.getHours() + ":";
      var m = date.getMinutes() + ":";
      var s = date.getSeconds();
      return Y + M + D + h + m + s;
    }
  },

  created() {
      this.jcfz(this.canshu)
      
  },
}
</script>
<style lang="less" scoped>
.dingdan{
.box-card{
   line-height:0px;
   margin-top:15px;
   text-align:left;
.el-cascader{
    width:100%;
}
}
}

</style>